#root {
    text-align: center;    
}

#main-container {
    text-align: center;
    flex:1;
    flex-direction: column;
    align-items: center;
}

.title {
    text-align: left;
    font-weight: bold;
    font-size: 35px;
    color: #388e3c;
    padding: 0 20px;
    margin-top: 70px;
}

.description {
    text-align: left;
    font-size: 28px;
    font-weight: 800;
    line-height: 1.5;
    padding: 0 20px;
    margin-top: 50px;
}

.button-container {
    margin-top: 70px;
    text-align: center;
}

.survey-button {
    width: 80%;
    max-width: 315px;
    color: #000;
    background-color: #eee;
    height: 100px;
    font-size: 20px;
    align-items: center;
    border-radius: 15px;
    margin-bottom: 20px;
    font-weight: 500;
    cursor: pointer;
    border: none;
    outline: none;
}

#progress-container {
    display: inline-block;
    text-align: center;
    width: 100%;
    max-width: 400px;
    margin-top: 50px;
}

#progress-text-container {
    font-weight: 500;
    font-size: 20px;
    color: #000;
    text-align: right;
}

.MuiLinearProgress-barColorPrimary {
    background-color: #388e3c !important;
}

.MuiLinearProgress-colorPrimary {
    background-color: #ccc !important;
}