/* 애니메이션 */
.motion {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	width: 200px;
	height: 200px;
	margin: auto;
}
.star {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	width: 20px;
	height: 20px;
	margin: auto;
}
.star::before {
	content: "";
	position: absolute;
	width: 100%;
	height: 100%;
	bottom: 50%;
	right: 50%;
	border: 8px solid #5a5;
	border-bottom: 0;
	border-right: 0;
	transform: skew(9deg, 9deg);
	transform-origin: 100% 100%;
}
.star>.star {
	transform : rotate(72deg);
}
.triangle {
	position: absolute;
	left: 0;
	right: 0;
	top: 0px;
	bottom: 20px;
	margin: auto;
	width: 100px;
	height: 10px;
	border-radius: 5px;
	background: #f5f;
}
.triangle:before, .triangle:after {
	content: "";
	position: absolute;
	width: 100%;
	height: 100%;
	border-radius: inherit;
    background: inherit;
    left: 0px;
}
.triangle:before {
	transform-origin: 5px 50%;
	transform: rotate(60deg);
}
.triangle:after {
	transform-origin: calc(100% - 5px) 50%;
	transform: rotate(-60deg);
}
.rectangle {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	margin: auto;
	width: 80px;
	height: 80px;
	border-radius: 5px;
	border: 10px solid #f55;
}
.circle {
	position: absolute;
	width: 140px;
	height: 140px;
	border-radius: 50%;
	box-sizing: border-box;
	border: 70px solid #5ff;
	left: 0;
	right: 0;
	top: -20px;
	bottom: -20px;
	margin: auto;
}
.rectangle2 {
	border-color: #55f;
}
.circle2 {
	width: 40px;
	height: 40px;
	bottom: 20px;
	border: 8px solid #EED414;
}
.star1 {
	top: 0;
	bottom: 20px;
}